<template>
  <div class="goodsOrder">
    <van-nav-bar title="确认订单" fixed @click-left="$toPage(-1)">
      <van-icon name="arrow-left" size="24px" color="#666666" slot="left" />
    </van-nav-bar>
    <div class="userInfo">
      <div class="userName" @click="showUser">
        <span>{{ userName }}</span>
        <span class="tel">{{ contactphone }}</span>
        <van-icon name="arrow" />
      </div>
      <p class="storeName">{{ this.userInfo.comp.storeName }}</p>
      <div class="address">
        <img src="../../assets/goodsDetails_icon/adress.png" />
        <p>{{ city + $loadUserInfo().comp.address }}</p>
      </div>
    </div>
    <div class="goodsInfo">
      <p class="title">药品信息</p>
      <div @click="showPage" class="goodsImg">
        <img
          v-for="(item, index) in goodsInfo.slice(0, 2)"
          v-lazy="item.imgURl"
          :key="index"
        />
        <span
          >共{{ flag ? goodsInfo.length + 1 : goodsInfo.length }}件
          <span v-if="goodsActiveInfo.length > 0" class="flag">(内含赠品)</span>
        </span>
        <van-icon name="arrow" />
      </div>
      <p class="total">
        <span>合计</span>
        <span>￥{{ amount.tprice }}</span>
      </p>
      <p class="total mask">
        <span>活动优惠</span>
        <span>-￥{{ amount.acvalue }}</span>
      </p>
    </div>
    <div class="discount">
      <div class="type" @click="onSelect">
        <span>发票类型</span>
        <van-icon name="arrow" />
        <span class="ordinary">{{ invoice }}</span>
      </div>
      <div class="type" @click="selectCoupon">
        <span>优惠券</span>
        <van-icon name="arrow" />
        <span class="ordinary"
          >{{ couponInfo.rulename }}，{{ couponInfo.reduce
          }}{{ couponInfo.ladderVOS[0].offers }}{{ couponInfo.symbol }}</span
        >
      </div>
      <div class="type blance">
        <span>余额抵扣</span>
        <van-switch
          v-model="isuseye"
          size="30px"
          active-color="#07c160"
          inactive-color="#f44"
          @change="onChange"
        />
        <span class="ordinary"
          >余额{{ amount.bal }}元，抵扣{{ amount.debal }}元</span
        >
      </div>
      <div class="remarks">
        <span>留言</span>
        <input type="text" v-model="input" />
      </div>
    </div>
    <div class="price">
      <div class="total">
        <span class="first">商品总额</span>
        <span>￥{{ amount.tprice }}</span>
      </div>
      <div class="total">
        <span class="first">邮费</span>
        <span>￥{{ amount.freeship ? 0 : goodsInfo[0].freight }}</span>
      </div>
      <div class="total">
        <span class="first">活动优惠</span>
        <span>￥{{ amount.acvalue }}</span>
      </div>
      <div class="total">
        <span class="first">优惠券抵扣</span>
        <span>￥{{ amount.cpvalue }}</span>
      </div>
      <div class="total">
        <span class="first">余额抵扣</span>
        <span>￥{{ amount.debal }}</span>
      </div>
      <van-checkbox v-model="checked" class="total" checked-color="#07c160">
        <p class="agree">
          我已阅读并同意签订<span @click.stop="agreementOpen"
            >《一块医药网上购销合同》</span
          >
        </p>
      </van-checkbox>
    </div>
    <van-submit-bar
      :disabled="!checked"
      button-text="提交订单"
      :loading="loading"
      @submit="onSubmit"
    >
      <div class="default" slot="default">
        <div>
          <span>应付金额：</span>
          <span class="last">￥{{ amount.acpay }}</span>
        </div>
        <p v-if="amount.rebeatp > 0">
          订单完成后，可返余额{{ amount.rebeatp }}元
        </p>
      </div>
    </van-submit-bar>
    <!-- 地址选择 -->
    <van-actionsheet
      class="showAddress"
      v-model="showAddress"
      title="选择收货人"
    >
      <p v-for="(item, index) in user" @click="selectUser(index)">
        <span>{{ item.contactname }}</span>
        <span>{{ item.contactphone }}</span>
      </p>
    </van-actionsheet>
    <!-- 选择发票方式 -->
    <van-actionsheet
      v-model="showFap"
      :close-on-click-overlay="overlay"
      :actions="actions"
      @select="onSelect"
    />
    <!-- 选择优惠券 -->
    <van-actionsheet
      v-if="goodsInfo.rule && goodsInfo.rule[0].rulecode == 1113 ? false : true"
      class="coupons"
      v-model="showCoupon"
      title="选择优惠券"
      :cancel-text="couponText"
      @cancel="onCancelCoupon"
    >
      <p
        v-for="(coupon, index) in coupons"
        :key="index"
        @click="onSelectCoupon(index)"
      >
        <van-tag plain type="danger">{{ coupon.rulename }}</van-tag
        >&nbsp&nbsp&nbsp&nbsp&nbsp
        <span v-for="(item, index) in coupon.ladderVOS" :key="index">
          满{{ item.ladamt }}元{{ coupon.reduce }}{{ item.offers
          }}{{ coupon.symbol }}&nbsp&nbsp&nbsp
        </span>
      </p>
    </van-actionsheet>
    <!-- 支付方式 -->
    <van-actionsheet
      class="payment"
      v-model="payment"
      :close-on-click-overlay="overlay"
    >
      <div
        v-for="(item, index) in payments"
        :key="index"
        @click="onSelectPay(index)"
      >
        <img :src="item.url" :alt="item.name" />
        <p class="name">{{ item.name }}</p>
      </div>
      <p class="cancel" @click="onCancel">稍后再付</p>
    </van-actionsheet>
    <transition name="agreeImg">
      <div v-if="agreeImg" class="agreeImg">
        <img src="../../assets/pay/consell-pic.png" />
      </div>
    </transition>
    <div v-if="agreeImg" class="agreeImgClose" @click="agreeClose">+</div>
  </div>
</template>
<script>
import { Dialog } from "vant";
export default {
  data() {
    return {
      isuseye: true,
      goodsInfo: [],
      price: 0,
      user: [],
      userName: "",
      contactphone: 0,
      city: "",
      input: "",
      amount: {},
      showAddress: false,
      showFap: false,
      invoice: "电子普通发票",
      invoiceCode: 1,
      actions: [
        { name: "电子普通发票", val: 1 },
        { name: "纸质普通发票", val: 2 },
        { name: "增值税专用发票", val: 3 }
      ],
      payment: false,
      payments: [
        {
          attr: "wxpay",
          name: "微信支付",
          url: require("../../assets/pay/wx.png")
        },
        {
          attr: "alipay",
          name: "支付宝支付",
          url: require("../../assets/pay/zfb.png")
        },
        {
          attr: "xxpay",
          name: "线下支付",
          url: require("../../assets/pay/xx.png")
        }
      ],
      showCoupon: false,
      coupons: [],
      couponUnqid: 0,
      couponCode: 0,
      couponInfo: {
        rulename: "未使用优惠券",
        reduce: "优惠",
        symbol: "元",
        ladderVOS: [
          {
            offers: 0
          }
        ]
      },
      couponInitial: {
        rulename: "未使用优惠券",
        reduce: "优惠",
        symbol: "元",
        ladderVOS: [
          {
            offers: 0
          }
        ]
      },
      overlay: false,
      orderno: 0, // 订单号
      goodsActiveInfo: [],
      flag: false,
      loading: false,
      taxpayer: true, //   纳税人是否存在
      placeType: 1,
      couponText: "不使用优惠券",
      checked: true, //   购销合同
      agreeImg: false //   合同img
    };
  },
  computed: {
    userInfo() {
      return this.$loadUserInfo();
    }
  },
  created() {
    //  获取详情页商品信息
    this.goodsInfo = JSON.parse(sessionStorage.getItem("placeOrderList"));
    //  获取活动信息
    this.goodsActiveInfo = JSON.parse(
      sessionStorage.getItem("activeOrderList")
    );
    this.getBlance(); // 金额
    this.getConsignee(); //  收货人
    this.reg_select_cityCode(); // 城市
  },
  mounted() {
    this.getCoupons(); //  优惠券
    this.getInvoiceInfo();
    this.placeType = this.$route.query.placeType
      ? this.$route.query.placeType
      : JSON.parse(sessionStorage.getItem("placeType"));
  },
  methods: {
    //  查询收货人
    getConsignee() {
      JNB.query({
        serverName: "userServer",
        cls: "MyDrugStoreInfoModule",
        method: "queryMyConsignee",
        params: {
          compid: this.userInfo.comp.storeId
        },
        callback: data => {
          let result = JSON.parse(data);
          if (result.code == 200) {
            this.user = result.data;
            if (this.user.length == 0) {
              this.$toast("请填写收货人！");
              setTimeout(() => {
                sessionStorage.setItem(
                  "placeType",
                  this.$route.query.placeType
                    ? this.$route.query.placeType
                    : JSON.parse(sessionStorage.getItem("placeType"))
                );
                this.$router.push({
                  path: "/addConsignee",
                  query: {
                    replace: true
                  }
                });
              }, 1000);
            } else {
              this.userName = this.user[0].contactname;
              this.contactphone = this.user[0].contactphone;
              this.user.forEach(arr => {
                if (arr.cstatus === 2) {
                  this.userName = arr.contactname;
                  this.contactphone = arr.contactphone;
                }
              });
            }
          }
        }
      });
    },
    //  城市码转城市名称
    reg_select_cityCode() {
      JNB._requestNative(
        "convertAreaCodeToFullName",
        this.userInfo.comp.addressCode,
        fullName => {
          this.city = fullName;
        }
      );
    },
    //  获取金额信息
    getBlance() {
      let list = this.goodsInfo.map(data => {
        return {
          pdno: data.pdno,
          pnum: data.num,
          compid: this.userInfo.comp.storeId,
          price: data.pdprice,
          amt: data.pdprice * data.num,
          coupon: this.couponUnqid,
          shipfee: data.freight,
          balway: this.isuseye ? 1 : 0,
          skprice: data.skprice
        };
      });
      JNB.query({
        serverName: "orderServer" + this.$ser_in(this.userInfo.comp.storeId),
        cls: "CouponRevModule",
        method: "CouponCalculate",
        params: list,
        callback: data => {
          let result = JSON.parse(data);
          if (result.code == 200) {
            this.amount = result.data;
          } else {
            this.$toast.fail(result.message);
            setTimeout(() => {
              // this.$router.replace({
              //   path: "/shoppingCart"
              // });
              this.$router.go(-1);
            }, 1000);
          }
        }
      });
    },
    //  是否使用余额抵扣
    onChange() {
      this.getBlance();
    },
    showUser() {
      this.showAddress = true;
    },
    //  选择收货人
    selectUser(index) {
      this.showAddress = false;
      this.userName = this.user[index].contactname;
      this.contactphone = this.user[index].contactphone;
    },
    // 选择发票方式
    onSelect(item) {
      // 有纳税人识别号才能选择
      if (this.taxpayer !== "") {
        this.showFap = !this.showFap;
        this.invoice = item.name;
        this.invoiceCode = item.val;
      }
    },
    //  商品详细信息
    showPage() {
      sessionStorage.setItem(
        "placeType",
        this.$route.query.placeType
          ? this.$route.query.placeType
          : JSON.parse(sessionStorage.getItem("placeType"))
      );
      this.$router.replace({
        path: "/drugDetails"
      });
    },
    //  显示优惠券信息
    selectCoupon() {
      this.showCoupon = true;
    },
    //  选择优惠券
    onSelectCoupon(i) {
      this.showCoupon = false;
      this.couponInfo = this.coupons[i];
      this.couponCode = this.coupons[i].coupno;
      this.couponUnqid = this.coupons[i].unqid;
      this.getBlance();
    },
    //  不使用优惠券
    onCancelCoupon() {
      this.couponInfo = this.couponInitial;
      this.couponUnqid = 0;
      this.getBlance();
    },
    //  获取优惠券信息
    getCoupons() {
      let arrCoupons = this.goodsInfo.map(data => {
        return {
          pdno: data.pdno,
          pnum: data.num,
          compid: this.userInfo.comp.storeId,
          price: data.pdprice,
          amt: (data.pdprice * 100 * (data.num * 100)) / 10000,
          samt: data.acamt + data.amt,
          flag: data.oflag ? 1 : 0,
          balway: this.isuseye ? 1 : 0
        };
      });
      JNB.query({
        serverName: "orderServer" + this.$ser_in(this.userInfo.comp.storeId),
        cls: "CouponRevModule",
        method: "queryActCouponList",
        params: arrCoupons,
        callback: data => {
          let result = JSON.parse(data);
          if (!result.data || result.data.length == 0) {
            this.couponText = "没有可使用的优惠券";
          }
          if (result.code === 200 && result.data) {
            this.coupons = result.data;
            this.coupons.forEach(data => {
              data.btn = "立即领取";
              if (data.brulecode === 2110) {
                data.reduce = "减";
                data.symbol = "元";
                data.ladderVOS[0].offers = data.ladderVOS[0].offer;
              } else if (data.brulecode === 2120) {
                data.reduce = "包邮";
                data.symbol = "";
                if (data.ladderVOS[0].offer === 0) {
                  data.ladderVOS[0].offers = "";
                }
              } else {
                data.reduce = "";
                data.symbol = "折";
                data.ladderVOS[0].offers = data.ladderVOS[0].offer;
              }
            });
          }
        }
      });
    },
    //  提交订单
    onSubmit() {
      this.loading = true;
      let obj = this.goodsInfo.map(data => {
        return {
          pdno: data.pdno,
          pnum: data.num,
          pdprice: data.pdprice,
          actcode: JSON.stringify(data.actcode)
        };
      });
      JNB.query({
        serverName: "orderServer" + this.$ser_in(this.userInfo.comp.storeId),
        cls: "TranOrderOptModule",
        method: "placeOrder",
        params: {
          goodsArr: obj,
          orderType: 0,
          placeType: this.placeType,
          balway: this.isuseye,
          coupon: this.couponCode,
          unqid: this.couponUnqid,
          orderObj: {
            remarks: this.input,
            cusno: this.userInfo.comp.storeId,
            busno: 536862720,
            consignee: this.userName, //  联系人
            contact: this.contactphone, //  电话
            rvaddno: this.userInfo.comp.addressCode,
            address: this.city,
            invoicetype: this.invoiceCode
          }
        },
        callback: data => {
          let result = JSON.parse(data);
          if (result.code === 200) {
            this.orderno = result.data.orderno;
            sessionStorage.removeItem("activeOrderList");
            this.showPayInfo();
          } else {
            this.loading = false;
            this.$toast.fail(result.message);
          }
        }
      });
    },
    //  订单状态
    showPayInfo() {
      JNB.query({
        serverName: "orderServer" + this.$ser_in(this.userInfo.comp.storeId),
        cls: "PayModule",
        method: "showPayInfo",
        params: {
          orderno: this.orderno,
          compid: this.userInfo.comp.storeId
        },
        callback: data => {
          let result = JSON.parse(data);
          if (result.code == 200) {
            this.payInfo = result.data;
            if (result.data.balflag == 1) {
              this.balAllPay();
            } else {
              this.payment = true;
              this.loading = false;
            }
          }
        }
      });
    },
    //  余额支付
    balAllPay() {
      JNB.query({
        serverName: "orderServer" + this.$ser_in(this.userInfo.comp.storeId),
        cls: "PayModule",
        method: "balAllPay",
        params: {
          orderno: this.orderno,
          compid: this.userInfo.comp.storeId
        },
        callback: data => {
          let result = JSON.parse(data);
          if (result.code == 200) {
            this.$router.replace({
              path: "/successfulTrade",
              query: {
                orderNo: this.orderno,
                back: 0
              }
            });
          } else {
            this.$toast.fail(result.message);
          }
        }
      });
    },
    //  选择支付方式
    onSelectPay(item) {
      if (item == 0 || item == 1) {
        JNB._requestNative(
          this.payments[item].attr,
          JSON.stringify({ orderno: this.orderno }),
          data => {
            if (data == 0) {
              if (this.$store.state.payResult) {
                Dialog.alert({
                  title: "温馨提示",
                  message: "支付成功！"
                }).then(() => {
                  this.$router.replace({
                    path: "/successfulTrade",
                    query: {
                      orderNo: this.orderno,
                      back: 0
                    }
                  });
                });
              }
            } else {
              Dialog.alert({
                title: "支付失败",
                message: "请到订单详情页继续完成支付~"
              }).then(() => {
                this.$router.replace({
                  path: "/successfulTrade",
                  query: {
                    orderNo: this.orderno,
                    back: 0
                  }
                });
              });
            }
          }
        );
      } else if (item == 2) {
        Dialog.confirm({
          title: "您确认付款方式为线下转账？",
          message: `一、请您在汇款时备注一块医药订单编号，这将会很大程度上缩短我们的核款时间并能尽快为您安排发货。<br>二、请于24小时内汇款并确保汇款金额与订单总金额一致，到账时间为1-3个工作日。<br>三、从下单之日起5天内如果还未付款并到账，系统将自动取消该订单。<br>四、我们将在收到款项后及时确认收款并安排发货，若款项汇出2天后未帮您收款确认，请及时拨打底部客服热线。`
        })
          .then(() => {
            JNB.query({
              serverName:
                "orderServer" + this.$ser_in(this.userInfo.comp.storeId),
              cls: "PayModule",
              method: "offlinePay",
              params: {
                orderno: this.orderno,
                compid: this.userInfo.comp.storeId,
                paytype: 4
              },
              callback: data => {
                let result = JSON.parse(data);
                if (result.code === 200) {
                  this.$router.replace({
                    path: "/successfulTrade",
                    query: {
                      orderNo: this.orderno,
                      back: 0
                    }
                  });
                } else {
                  this.$toast.fail(result.message);
                }
              }
            });
          })
          .catch(() => {
            // on cancel
          });
      }
    },
    onCancel() {
      Dialog.confirm({
        title: "温馨提示",
        message: "订单已提交，请尽快支付哟~"
      })
        .then(() => {
          this.$router.push({
            path: "/successfulTrade",
            query: {
              orderNo: this.orderno,
              back: 0
            }
          });
        })
        .catch(() => {
          // on cancel
        });
    },
    //  获取开票信息
    getInvoiceInfo() {
      JNB.query({
        serverName: "userServer",
        cls: "MyInvoiceModule",
        method: "getInvoice",
        params: {},
        callback: data => {
          const result = JSON.parse(data);
          if (result.data.taxpayer == "") {
            this.taxpayer = false;
          }
        }
      });
    },
    agreementOpen() {
      this.agreeImg = true
      this.scrollTop = document.documentElement.scrollTop || document.body.scrollTop
      document.body.scrollTop = 0;
      document.documentElement.scrollTop = 0;
    },
    agreeClose() {
      document.body.scrollTop = this.scrollTop;
      document.documentElement.scrollTop = this.scrollTop;
      this.agreeImg = false
    }
  }
};
</script>
<style scoped>
p {
  margin: 0;
  padding: 0;
}
.goodsOrder {
  background-color: #f5f5f5;
  padding: 86px 0 110px;
  box-sizing: border-box;
}
.goodsOrder .userInfo {
  width: 710px;
  background: rgba(255, 255, 255, 1);
  border-radius: 8px;
  margin: 20px auto;
}
.goodsOrder .userInfo .userName {
  padding: 30px 0 0;
}
.goodsOrder .userInfo .userName span:first-child {
  font-size: 32px;
  color: rgba(51, 51, 51, 1);
  margin: 0 20px 0 100px;
  font-weight: bold;
}
.goodsOrder .userInfo .userName .tel {
  font-size: 28px;
  color: rgba(153, 153, 153, 1);
}
.goodsOrder .userInfo .userName .van-icon {
  float: right;
  margin: 10px 20px 0 0;
  font-size: 28px;
  font-weight: bold;
  color: rgba(153, 153, 153, 1);
}
.goodsOrder .userInfo .storeName {
  font-size: 28px;
  color: rgba(51, 51, 51, 1);
  margin: 20px 100px 0;
}
.goodsOrder .userInfo .address {
  overflow: hidden;
}
.goodsOrder .userInfo .address img {
  width: 56px;
  height: 56px;
  border-radius: 50%;
  padding: 20px;
  float: left;
}
.goodsOrder .userInfo .address p {
  width: 550px;
  height: 96px;
  font-size: 28px;
  color: rgba(51, 51, 51, 1);
  float: left;
  margin-left: 3px;
  display: flex;
  align-items: center;
  /* line-height: 96px; */
  /* word-wrap:break-word;
  word-break:break-all; */
  /* overflow: hidden;
  text-overflow:ellipsis;
  white-space: nowrap; */
}
.goodsOrder .goodsInfo {
  width: 710px;
  background: rgba(255, 255, 255, 1);
  border-radius: 8px;
  margin: auto;
  padding-bottom: 20px;
}
.goodsOrder .goodsInfo .title {
  padding: 20px;
  font-size: 32px;
  color: rgba(51, 51, 51, 1);
  border-bottom: 1px solid rgba(230, 230, 230, 1);
}
.goodsOrder .goodsInfo .goodsImg img {
  width: 120px;
  height: 120px;
